  import day from '@/components/use/day'
  import month from '@/components/use/month'
  import list from '@/components/use/list'
export default {
  name: 'main',
  data () {
    return {
      msg: "",
      option1: '是',
      options1: ['是', '否'],
      isShow:0
    }
  },
  components:{
    day,
    month,
    list
  },
  methods:{
    onItemClick(val){
      this.isShow=val
      if(val === 1){
        this.$refs.initMon.init()
      }else if (val === 2) {
        this.$refs.initList.init()
      }
    }
  }
}



